<template>
    <div class="info-div" style="height: 100%;">
      <div class="info-header">
        <span class="callback" @click="resetComponent">设备档案 </span>
        <span> > 设备详情</span>
      </div>
        <div class="info-container" v-if="show">
          <div class="list-content detail-content list-contentB" >
            <div class="list-header">
              <i class="hasIcon hasImg">
                <img src="dist/img/file/base-infor-icon.png" />
              </i>基础信息
              <!--controls[2]-->
              <!--deviceType === '04' && controls[16] deviceType === '04'  && controls[17] deviceType === '05' && controls[23] deviceType === '05'&& controls[24] deviceType === '05'&& controls[25] deviceType === '05' && controls[26]-->
              <button   class="list-operate"  v-if="deviceType === '04'&& controls[16] "  @click="resetJZQ()">
                <i class="hasIcon"><icon name="repeat"></icon></i>
                重启
              </button>
              <button   class="list-operate"  v-if="deviceType === '04'  && controls[17] " @click="restartJZQ()">
                <i class="hasIcon"><icon name="refresh"></icon></i>
                初始化
              </button>
              <button   class="list-operate" v-if="deviceType === '06'  && controls[23] "  @click="operateZHQ('重启中', 'deviceInfo/converterReboot', null, false)">
                <i class="hasIcon"><icon name="repeat"></icon></i>
                重启
              </button>
              <button   class="list-operate" v-if="deviceType === '06' && controls[24]"   @click="operateZHQ('初始化', 'deviceInfo/converterInit', null, false)">
                 <i class="hasIcon"><icon name="refresh"></icon></i>
                初始化
              </button>
              <button   class="list-operate" v-if="deviceType === '06'&& controls[25]"   @click="operateZHQ('读取时钟', 'deviceInfo/readConverterTime', '当前系统时间', true)">
                <i class="hasIcon"><icon name="hourglass-half"></icon></i>
                读时钟
              </button>
              <button   class="list-operate" v-if="deviceType === '06' && controls[26] "   @click="operateZHQ('设置时钟', 'deviceInfo/setConverterTime', null, false)">
                <i class="hasIcon"><icon name="clock-o"></icon></i>
                设时钟
              </button>
              <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="editDeviceInfo('编辑集中器', 'commInfo', 'deviceInfo/editJzqBaseInfo')" >
                <i class="hasIcon"><icon name="edit"></icon></i>
                    编辑
              </button>
            </div>
            <ul  class="form-list detail-list">
              <li v-for="(items, $indexs) in lists.commInfo"  v-if="!items.unShow && lists.commInfo" >
                <span class="label-span" style="min-width: 7em">
                  {{items.name}} :&nbsp;&nbsp;
                </span>
                <span :title="items.value" class="label-span" style="max-width: calc(100% - 8em)">{{getValue(items.value)}}</span>
              </li>
            </ul>
          </div>
          <div  class="list-content detail-content list-contentB" v-if="deviceType === '04'" >
            <div class="list-header">
              <i class="hasIcon hasImg"><img src="dist/img/file/IP-icon.png" /></i>主站IP和端口
             <!--  <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="redConfig()" >
                <i class="hasIcon"><icon name="podcast"></icon></i>
                读取
              </button> -->
            <!--   <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="issueJZQ('ipPort','deviceInfo/jzqConnectParamSend')" >
                <i class="hasIcon"><icon name="sign-in"></icon></i>
                下发
              </button> -->
              <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="editDeviceInfo('编辑主站IP和端口', 'ipPort', 'deviceInfo/editJzqConnectionParam')" >
                <i class="hasIcon"><icon name="edit"></icon></i>
                编辑
              </button>
            </div>
            <ul  class="form-list detail-list">
              <li v-for="(items, $indexs) in lists.ipPort" style="margin:0.05em 1em 0.05em 0;">
                 <span class="label-span" style="min-width: 7em">
                  {{items.name}} :&nbsp;&nbsp;
                </span>
                <span :title="items.value" class="label-span" style="max-width: calc(100% - 8em)">
                  {{getValue(items.value)}}
                </span>
              </li>
            </ul>
          </div>
          <div  class="list-content detail-content list-contentB" v-if="deviceType === '04'" >
            <div class="list-header">
              <i class="hasIcon hasImg"><img src="dist/img/file/center-icon.png" /></i>集中器心跳
              <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="redConfig('heart','deviceInfo/jzqHeartBeatRead')" >
                <i class="hasIcon"><icon name="podcast"></icon></i>
                读取
              </button>
              <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="issueJZQ('heart','deviceInfo/jzqHeartBeatSend')"  >
                <i class="hasIcon"><icon name="sign-in"></icon></i>
                下发
              </button>
              <button  v-if="deviceType === '04'&& controls[2]"  class="list-operate" @click="editDeviceInfo('编辑集中器心跳', 'heart','deviceInfo/editJzqHeartBeat')" >
                <i class="hasIcon"><icon name="edit"></icon></i>
                编辑
              </button>
              <button @click="heartData(true)" class="list-operate" >
                    <i  class="hasIcon " ><icon name="heartbeat" ></icon></i>
                    心跳
              </button>
            </div>
            <ul  class="form-list detail-list">
              <li v-for="(items, $indexs) in lists.heart" style="margin:0.05em 1em 0.05em 0;">
                 <span class="label-span" style="min-width: 7em">
                  {{items.name}} :&nbsp;&nbsp;
                </span>
                <span :title="items.value" class="label-span"  style="max-width: calc(100% - 8em)">
                  {{getValue(items.value)}}
                </span>
              </li>
            </ul>
          </div>
        </div>
        <loginAgain ref="loginAgain" @nextOperate="nextOperate"></loginAgain>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="loading" ref="loadings"></dialogs>
      <dialogs type="customer" ref="editJZQ" width="43em" v-if="dataReady">
        <div slot="header">
           {{currentEdit.title}}
        </div>
        <div slot="content">
          <ul class="dialog-ul">
            <li v-for="(item,$index) in editList[currentEdit.item]" class=" formList " :class="item.classes" v-if="dataReady && typeof editList[currentEdit.item] !== 'undefined'">
              <label v-if="!item.isEmpty" class="label-show"><span class="emptyTips"> * </span> {{item.label}} </label>
              <label v-if="item.isEmpty" class="label-show"><span >&nbsp;</span>{{item.label}} </label>
              <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
              <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
            </li>
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="saveEdit">提交</button>
          <button class="button-default cancel-button" @click="cancelEdit">取消</button>
        </div>
      </dialogs>
      <dialogs type="customer" width="55em" maxHeight="620px" ref="heartDataDia">
           <div slot="header">
              心跳列表
           </div>
           <div slot="content">
              <tables ref="caution"  class="dialogTable" :tableOpt="TableOpt" @freshStaticTable="heartData(false)" ></tables>
           </div>
      </dialogs>
    </div>
</template>
<script>
    import loginAgain from '../../childComponent/userValidate.vue'
    import {GUID} from '../../../assets/GUID.js'
    import 'vue-awesome/icons/repeat'
    import 'vue-awesome/icons/sign-in'
    import 'vue-awesome/icons/podcast'
    import 'vue-awesome/icons/clock-o'
    import 'vue-awesome/icons/hourglass-half'
     import 'vue-awesome/icons/heartbeat'
    export default{
        data() {
            return {
               
                setRecall: null,
                currentEdit:{
                   title: '编辑集中器',
                   item: 'commInfo',
                   url: ''
                },
                dataReady: false,
                submitControl: false,
                operateNow: -1,  //0:集中器初始化 1：集中器重启 2：采集器操作
                show: true,
                operateMsg: {
                    msg: '',
                    sign: ''
                },
                showCJFS: false,
                btnMsg: '修改',
                deviceType: '', //设备类型
                deviceInfo: {
                },
                commInfo: {
                    deviceNo: '',
                    deviceFactory: '',
                    installAddr: '',
                    installDate: '',
                    remark: ''
                },
                ipPort: {
                  deviceNo: '',
                  websiteIp: '',
                  websitePort: '',
                  apn: '',
                  spareIP: '',
                  sparePort: ''
                },
                heart: {
                  deviceNo: '',
                  hbCycle: ''
                },
               TableOpt: {
                    column:[
                        {title: '编号', field: '', textAlign: 'center', width: '120',
                        type: 'string', formatter: function (val, index,item, obj, numIndex, start) {
                            
                            return numIndex + start
                        }},
                        {title: '时间', field: 'createTime', textAlign: 'center', width: '150'},
                        {title: '状态', field: 'state', align: 'center', textAlign: 'middle', width: '150', type: 'string', formatter: function (val) {
                            if(val !== null && typeof val !== 'undefined'){
                              let vals = val.toString();
                              switch(vals) {
                                 case '0':
                                   return '<div class="red">掉线</div>';
                                 case '1':
                                   return '<div >登录</div>';
                                 case '2':
                                   return '<div class="green">心跳</div>'
                              }
                            }
                        }}
                    ],
                    data: [],
                    height: 300,
                    pageSize: 50,
                    hasRefresh: true,
                    pageNumber: 1
                }
            }
        },
        components: {
            loginAgain
        },
        props: ['row', 'controls'],
        methods: {
          heartData: function(flag) {
              let that = this;
              that.getRequest({
                 url: 'deviceInfo/queryJzqStateDetail',
                 method: 'post',
                // url: 'dist/json/deviceUse.json',
                // method: 'get',
                param: {
                   deviceCno: that.row.deviceCno
                },
                success: function (data) {
                  that.$refs.caution.resetTableOpt({data: data.data});
                  if(flag) {
                    that.showHeartData();
                  }
                },
                error: function () {
                  that.$refs.caution.resetTableOpt({data: []});
                 if(flag) {
                    that.showHeartData();
                  }
                }
              }, true)
            },
          showHeartData: function () {
              this.$refs.heartDataDia.show({
                  hasClose: true
              })
          },
          redConfig: function (key, url) {
              let alerts = this.$refs.alerts;
              let loadings = this.$refs.loadings;
              let guid = new GUID().newGUID();
              let that = this;
              loadings.show({
                msg: '正在读取参数，请稍后。。。',
                hasClose: true,
                action: {
                  cancel: function () {
                    that.showCloseInfo = false
                  }
                }
              });
              let arr = that[key];
              arr['guid'] = guid;
              arr['jzqNo'] = that.deviceInfo.deviceNo;
              that.getRequest({
                url: url,
                method: 'post',
                param: arr,
                success: function (){
                  that.getWebsocket({
                    keyMsg: 'jzqHeartBeatRead',
                    keyStatus: 'jzqHeartBeatReadStatus',
                    paramStatus: {
                      guid: guid,
                      dataFlag: 'jzqHeartBeatReadStatus'
                    },
                    funMsg: function (data) {
                      that.redConfigResult(data)
                    }
                  })
                 
                },
                error: function (data) {
                  loadings.close();
                  alerts.show({
                    msg: data.message
                  })
                }
              }, true)
            },
           redConfigResult: function (data) {
              let that = this;
              let alerts = that.$refs.alerts;
              if( that.showCloseInfo ) {
                  that.$refs.loadings.close();
                  let Data = data.data;
                  let msg = Data.message;
                  if(Data.status == 1) {
                      msg = msg + '，心跳：'+ Data.hbCycle;
                  }
                  alerts.show({msg: msg});
              }
              that.$store.state.callback['jzqHeartBeatRead'] = null;
              that.$store.state.callback['jzqHeartBeatReadStatus'] = null;
              clearInterval(that.setRecall);
              that.showCloseInfo = true;
           },
            //心跳端口下发
           issueJZQ:function (key, url) {
              let guid = new GUID().newGUID();
              let alerts = this.$refs.alerts;
              let loadings = this.$refs.loadings;
              let that = this;
              loadings.show({
                msg: '正在下发参数，请稍后。。。'
              });
              let arr = that[key];
              arr['jzqNo'] = that.deviceInfo.deviceNo;
              arr['guid'] = guid;
              that.getRequest({
                url: url,
                method: 'post',
                param: arr,
                success: function (){
                  let keys = key == 'heart' ? 'jzqHeartBeatSend' : 'jzqConnectionParamSend';
                  that.getWebsocket({
                    keyMsg: keys,
                    keyStatus: keys + 'Status',
                    paramStatus: {
                      guid: guid,
                      dataFlag: keys + 'Status'
                    },
                    funMsg: function (data) {
                      that.issueResult(data, keys)
                    }
                  })
                
                },
                error: function (data) {
                  loadings.close();
                  alerts.show({
                    msg: data.message
                  })
                }
              }, key == 'heart');
            },
            issueResult: function (data, keys) {
              let that = this;
              that.$refs.loadings.close();
              that.$refs.alerts.show({msg: data.data});
              that.$store.state.callback[keys] = null;
              that.$store.state.callback[keys + 'Status'] = null;
              clearInterval(that.setRecall);
            },
            getFrom: function (val) {
              if(typeof val.val === 'undefined')  {
                val['val'] = '';
              }
               this.getOption(val, this);
            },
            editDeviceInfo: function (title, item, url) {
              this.currentEdit.title = title;
              this.currentEdit.item = item;
              this.currentEdit.url = url;
              this.$refs.editJZQ.show();
            },
            cancelEdit: function () {
                this.$refs.editJZQ.close();
                this.getInfoData();
            },
            //验证完用户身份的后续操作
            nextOperate: function () {
                if (this.operateNow === 0) {
                    this.restartJZQ(); //集中器初始化
                } else if (this.operateNow === 1) {
                    this.resetJZQ();  //集中器重启
                } else if (this.operateNow === 2) {
                    //采集器操作
                    this.operateZHQ(this.operateMsg.msg, this.operateMsg.url, this.operateMsg.title, this.operateMsg.showMsg);
                }
            },
            //返回列表
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            ///保存修改数据
            saveEdit: function () {
                let alerts = this.$refs.alerts;
                let that = this;
                let p = this[this.currentEdit.item];

                let require =  this.currentEdit.item === 'ipPort' ? ['websiteIp', 'websitePort', 'apn'] : (this.currentEdit.item === 'heart' ? ['hbCycle'] : []);

                this.regularTest({
                    emptyKey: require,
                    prefix: that.currentEdit.item,
                    callback: function () {
                        let loadings = that.$refs.loadings;
                        loadings.show({
                          msg: '正在提交，请稍等...'
                        });

                        that.getRequest({
                            url: that.currentEdit['url'],
                            method: 'post',
                            param: that[that.currentEdit.item],
                            success: function (data) {
                              loadings.close();
                              alerts.show({msg: data.message});
                              that.cancelEdit();
                            },
                            error: function (data) {
                              loadings.close();
                              alerts.show({msg: data.message});
                              that.cancelEdit();
                            }
                        }, that.currentEdit['item'] === 'heart')
                    }
                })

            },
            operateDevice: function (msg, url, params, title, showMsg) {
                let alerts = this.$refs.alerts;
                let loadings = this.$refs.loadings;
                let that = this;
                loadings.show({
                  msg: msg
                });
                this.getRequest({
                        url: url,
                        method: 'post',
                        param: params,
                        success: function (data) {
                          let titles = '系统提示';
                          if (title !== null) {
                            titles = title
                          }
                           loadings.close();
                            alerts.show({
                              titleMsg: titles,
                              msg: showMsg ? '<span>当前服务时间： <span style = "color: #149F5C">' + data.data.serverTime +
                                '</span>；<br/>当前转换器时间： <span style="color:#D84C29;margin-top:0.5em">' + data.data.converterTime + '</span></span>' : data.message
                            });
                            that.operateNow = -1;
                        },
                     error: function (data) {
                       loadings.close();
                       alerts.show({msg: data.message});
                       that.operateNow = -1;
                     }
                }, true)
            },
            //集中器初始化
            restartJZQ: function () {
                this.operateNow = 0;
                let p = {jzqNo: this.row.deviceNo, type: '0'};
                this.operateDevice('初始化进行中...', 'deviceInfo/jzqInit', p, null, false);
            },
            resetJZQ: function () {
                this.operateNow = 1;
                let p = {jzqNo: this.row.deviceNo};
                this.operateDevice('终端重启中...', 'deviceInfo/jzqReboot', p, null, false);
            },
            //采集器的对应操作  type: 1：重启 2： 初始化  0： 读时钟 1：设置时钟
            //title: 设置弹出框标题
            //showMsg： 自定义显示标题
           //url: 对应的URI地址
            operateZHQ: function (msg, url, title, showMsg) {
                this.operateMsg.msg = msg;
                this.operateMsg.url = url;
                this.operateMsg.title = title;
                this.operateMsg.showMsg = showMsg;
                this.operateNow = 2;
                let p = {converterNo: this.row.deviceNo};
                this.operateDevice('正在' + this.operateMsg.msg + '，请稍后...', url, p, title, showMsg);
            },
            //获取设备详情
            getInfoData: function () {
                let data = {deviceNo: this.row.deviceNo};
                let that = this;
                let url = '';
              switch (this.deviceType) {
                case '04':
                  url = 'deviceInfo/queryJzqDetail';
                  break;
                case '07':
                  url = 'deviceInfo/queryElectricDetail';
                  break;
                case '08':
                  url = 'deviceInfo/queryWaterDetail';
                  break;
                case '09':
                  url = 'deviceInfo/queryGasDetail';
                  break;
                case '05':
                  url = 'deviceInfo/queryCjqDetail';
                  break;
                case '06':
                  url = 'deviceInfo/queryConverterDetail';
                  break;
              }
                this.getRequest({
                    url: url,
                    method: 'post',
                    param: data,
                    success: function (data) {
                        that.deviceInfo = data.data;
                        that.dataReady = true;
                        if(that.deviceType === '04') {
                            for(let key1 in that.commInfo) {
                              that.commInfo[key1] = that.deviceInfo[key1];
                            }
                            for(let key2 in that.ipPort) {
                              that.ipPort[key2] = that.deviceInfo[key2]
                            }
                            for(let key3 in that.heart) {
                              that.heart[key3] = that.deviceInfo[key3]
                            }
                          }
                        }
                }, true)
            },
            init: function () {
                this.deviceType = this.row.deviceType;
                this.getInfoData();
            }
        },
        computed: {
            lists: function () {
                let deviceInfos;
                switch (this.deviceType) {
                    case '04': {
                        deviceInfos = [
                            {
                              name: '集中器号',
                              value: this.deviceInfo.deviceNo,
                            },
                            {
                              name: '部门机构',
                              value: this.deviceInfo.orgName,
                             },
                            {
                              name: '设备厂家',
                              value: this.deviceInfo.deviceFactory
                            },
                            {
                              name: '安装时间',
                              value: this.deviceInfo.installDate
                            },
                           {
                              name: '安装位置',
                              value: this.deviceInfo.installAddr
                            },
                            {
                              name: '备注信息',
                              value: this.deviceInfo.remark
                            }
                        ];
                        break;
                    }
                    case '05': {
                        deviceInfos = [
                            {
                              name: '采集器号',
                              value: this.deviceInfo.deviceNo,
                            },
                            {
                              name: '设备厂家',
                              value: this.deviceInfo.deviceFactory
                            },
                            {
                              name: '安装时间',
                              value: this.deviceInfo.installDate
                            },
                            {
                             name: '备注信息',
                             value: this.deviceInfo.remark
                           }
                        ];
                        break
                    }
                    case '06': {
                    deviceInfos = [
                      {
                        name: '转换器号',
                        value: this.deviceInfo.deviceNo,
                      },
                      {
                        name: '设备厂家',
                        value: this.deviceInfo.deviceFactory
                      },
                      {
                        name: '安装时间',
                        value: this.deviceInfo.installDate
                      },
                      {
                        name: '备注信息',
                        value: this.deviceInfo.remark
                      }
                    ];
                    break
                  }
                    default: {
                        let deviceName = '';
                        if (this.deviceType === '07') {
                            deviceName = '电表';
                        } else if (this.deviceType === '08') {
                            deviceName = '水表';
                        } else if (this.deviceType === '09') {
                            deviceName = '气表'
                        }
                        deviceInfos = [
                          {
                            name:'用户姓名',
                            value: this.deviceInfo.customerName,
                          },
                          {
                            name:'商家品牌',
                            value: this.deviceInfo.customerBrand,
                          },
                          {
                            name:'门牌编号',
                            value: this.deviceInfo.propertyName,
                          },
                          {
                            name:'联系电话',
                            value: this.deviceInfo.customerContact,
                          },
                          {
                            name:'用户地址',
                            value: this.deviceInfo.customerAddr,
                          },

                          {
                            name: '部门机构',
                            value: this.deviceInfo.orgName
                          },
                          {
                            name: deviceName + '表号',
                            value: this.deviceInfo.deviceNo,
                          },
                          {
                            name: '表计户号',
                            value: this.deviceInfo.meterUserNo
                          },
                          {
                            name: '下发状态',
                            value: this.getEnumItem('sendFlag', this.deviceInfo.sendFlag).text
                          },
                          {
                            name: '采集方式',
                            value: this.getEnumItem('commPort', this.deviceInfo.commPort).text
                          },
                          {
                            name: deviceName + '规约',
                            value: this.deviceType === '07' ? this.getEnumItem('commRule',this.deviceInfo.commRule + '-' + this.deviceInfo.meterType).text :
                              this.getEnumItem('commRules',this.deviceInfo.commRule).text
                          },
                          {
                            name: !(this.deviceInfo.moteType === 'C' && this.deviceType !== '07') ? '采集器号' : '转换器号',
                            value: this.deviceInfo.commCollectionNo
                          },
                          {
                            name: '变压器号',
                            value: this.deviceInfo.transformerNo
                          },
                          {
                            name: '集中器号',
                            value: this.deviceInfo.jzqNo
                          },
                       
                         
                          // {
                          //   name: '预留金额',
                          //   value: this.deviceInfo.initAmount,
                          //   unShow: !(this.deviceInfo.localControl === 2 || (this.deviceType !== '07'))
                          // },
                          {
                            name: '计费类型',
                            value: this.deviceInfo.dictItemName,
                          },
                          //  {
                          //     name: '告警金额1',
                          //     value: this.deviceInfo.alarmThreshold
                          // },
                          // {
                          //     name: '告警金额2',
                          //     value: this.deviceInfo.alarmThreshold1
                          // },
                          {
                            name: '通信地址',
                            value: this.deviceInfo.deviceNo
                          },
                          {
                            name: '测量点',
                            value: this.deviceInfo.commPointCode
                          },
                          {
                            name: '重点用户',
                            value: this.deviceInfo.isImportant == 1 ? '是' : '否'
                          },
                           {
                            name: '楼栋编号',
                            value: this.deviceInfo.buildingNo
                          },
                          
                          {
                            name: '生产厂家',
                            value: this.deviceInfo.deviceFactory
                          },
                          
                          // {
                          //   name: '备注信息',
                          //   value: this.deviceInfo.remark
                          // },
                          {
                            name: '资料备注',
                            value: this.deviceInfo.remark2
                          },
                          {
                            name: '建档时间',
                            value: this.deviceInfo.createTime
                          },
                          {
                            name: '安装时间',
                            value: this.deviceInfo.installDate
                          }
                        ];
                        if (this.deviceType === '07') {
                            deviceInfos.splice(14, 0,
                                {
                                    name: '电流互感变比',
                                    value: this.deviceInfo.ratio,
                                    unShow: this.deviceInfo.localControl === 2,
                                },
                                {
                                    name: '费控方式',
                                    value: this.getEnumItem('localControl', this.deviceInfo.localControl).text
                                },
                                {
                                    name: '支持阀控',
                                    value: this.deviceInfo.isValveControl == 1 ? '支持' : '不支持'
                                }
                               
                            )
                            deviceInfos.splice(22, 0 ,
                                {
                                    name: '设备型号',
                                    value: this.deviceInfo.meterMode
                                },
                                 {
                                    name: '设备类别',
                                    value: this.deviceInfo.meterCategory
                                },
                                {
                                    name: '用电类型',
                                    value: this.deviceInfo.elecTypeName
                                },
                                {
                                    name: '计量类型',
                                    value: this.deviceInfo.elecMeterCategoryName
                                },
                                {
                                    name: '计量点',
                                    value: this.deviceInfo.commSetupSn
                                },
                                {
                                    name: '波特率',
                                    value: this.deviceInfo.commBaudrate
                                },
                                
                                 {
                                    name: '费率数',
                                    value: this.deviceInfo.commFactorCnt
                                },
                                 {
                                    name: '密钥等级',
                                    value: this.deviceInfo.keyGrade
                                },
                                 {
                                    name: '操作员代码',
                                    value: this.deviceInfo.meterUserName
                                },
                                 {
                                    name: '密码',
                                    value: this.deviceInfo.meterUserPwd
                                }
                                
                            )
                        }
                    }
                }
                return {
                    commInfo: deviceInfos,
                    ipPort: [
                        {
                            name: '主站端口',
                            value: this.deviceInfo.websitePort|| ''
                        },
                       {
                            name: '主站IP',
                            value: this.deviceInfo.websiteIp|| ''
                        },
                       {
                            name: 'APN',
                            value: this.deviceInfo.apn|| ''
                       },
                      {
                            name: '备用IP',
                            value: this.deviceInfo.spareIp|| ''
                      },
                      {
                            name: '备用端口',
                            value: this.deviceInfo.sparePort|| ''
                      }
                    ],
                    heart: [
                      {
                        name: '心跳时间',
                        value: this.deviceInfo.hbCycle
                      }
                    ]
                }
            },
            editList() {
            let width = 'calc(100% - 6.5em)';
            let data = this.deviceInfo;
            if (this.dataReady) {
              return {
                  commInfo:  [
                    {classes: 'halfWidth', type: 'input', isEmpty: false, label: '集中器号',
                      options: {type: 'disabledInput',  key: 'commInfo.deviceNo', width: width,value: data.deviceNo
                      }},
                    {classes: 'halfWidth hasMarginLi',type: 'select', isEmpty: false, label: '部门机构',
                      options: {
                        key: 'commInfo.orgNo',
                        type: 'complexSelect',
                        cs: 'server',
                        url: 'org/queryOrgTreeInfo',
                        method: 'post',
                        width: width,
                        constant: {orgNo: data.orgNo, orgName: data.orgName},
                        text: 'orgName',
                        value: 'orgNo'
                      }},
                    {classes: 'halfWidth', type: 'input', isEmpty: true, label: '设备厂家', options: {type: 'normalInput',   value: data.deviceFactory, width: width,placeHolder: '50个字符内', key: 'commInfo.deviceFactory',length: 50}},
                    {classes: 'halfWidth hasMarginLi',type: 'timer', isEmpty: true, label: '安装日期', options: {key: 'commInfo.installDate',  width: width, value: data.installDate}},
                    {classes: 'allWidth ',type: 'input', isEmpty: true, label: '安装位置', options: {type: 'textArea', width: width, placeHolder: '50个字符内',length: 50,  key: 'commInfo.installAddr', value: data.installAddr}},
                    {classes: 'allWidth',type: 'input', isEmpty: true, label: '备注信息', options: {type: 'textArea', width: width,  placeHolder: '200个字符内', key: 'commInfo.remark',  value: data.remark, length: 200}}
                  ],
                  ipPort:  [
                    {classes: 'halfWidth',type: 'input', isEmpty: false, label: '主站IP', options: {type: 'normalInput',  width: width, value: data.websiteIp, key: 'ipPort.websiteIp', regular: 'ip', length: 15, errorMsg: '主站IP：如10.10.10.250'}},
                    {classes: 'halfWidth hasMarginLi',type: 'input', isEmpty: false ,label: '主站端口', options: {type: 'normalInput',  width: width,value: data.websitePort, key: 'ipPort.websitePort', length: 4, regular: 'port', errorMsg: '主站端口：4位数字，如8080', limit: 'number'}},
                    {classes: 'halfWidth',type: 'input', isEmpty: true, label: '备用IP', options: {type: 'normalInput',  width: width, value: data.spareIp, key: 'ipPort.spareIp', regular: 'ip', length: 15, errorMsg: '备用IP：如10.10.10.250'}},
                    {classes: 'halfWidth hasMarginLi',type: 'input', isEmpty: true ,label: '备用端口', options: {type: 'normalInput',  width: width,value: data.sparePort, key: 'ipPort.sparePort', length: 4, regular: 'port', errorMsg: '备用端口：4位数字，如8080', limit: 'number'}},
                    {classes: 'allWidth',type: 'input', isEmpty: false, label: '通道APN', options: {type: 'normalInput',  width: width,value: data.apn, key: 'ipPort.apn', regular: 'apn', length: 10,  errorMsg: '通道APN：大写英文', limit: 'upperCase'}}
                  ],
                heart:  [
                  {classes: 'allWidth',type: 'input', isEmpty: false, label: '心跳时间', options: {type: 'normalInput', width: width,  placeHolder: '1-4位数字', key: 'heart.hbCycle',  value: data.hbCycle, length: 4}},
                ]
              };
            }
          }
        },
        mounted: function () {
            this.isIn();
            this.init();
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    button.cardOperate {
        padding:5px 12px;
        margin:5px 10px;
        float:left
    }
</style>



