<template>
    <div ref="trys" style="height: calc(100%)">
        <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls"></info>
        </div>
        <div v-show="!showInfo"  class="all-container noback-Container">
          <searchTree  @getSingleNode="getSelectNode" :noCustomer="true" @noData="noDevice" :singleSelect="true" @getDeviceType="getMeterType" @getIsOnline="getIsOnline"   :hasStatus="true" :hasInitSelect="true"></searchTree>
          <div class="TreeRight">
             <div class="operate-div operateBtn" >
                <!--v-if="controls[1]" v-if="controls[3]" -->
             <!--    <button class="button-operate" @click="addEquipment" v-if="controls[1]">
                   <i class="hasIcon hasImg">
                     <img src="dist/img/file/add-equipment-button.png" />
                    </i>添加设备
                </button> -->
                <!--v-if="controls[3]" -->
                <button class="button-operate" @click="removeEquipment" v-if="controls[3]">
                   <i class="hasIcon hasImg">
                      <img src="dist/img/file/delete-equipment-button.png" />
                   </i>删除设备
                 </button>
              </div>
              <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
                 <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"  v-if="showTable"
                    @rowDbClick="clickTableRow"></tables>
              </div>
          </div>
        </div>
      <dialogs type="confirm" ref="conformDialog"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="customer" ref="addEquip" v-if="showDialog" width="46em">
        <div slot="header">
          添加设备
        </div>
        <div slot="content">
          <ul class="dialog-ul">
            <li v-for="(item,$index) in addList" class=" formList " :class="item.classes">
              <label v-if="!item.isEmpty" class="label-show"><span class="emptyTips"> * </span> {{item.label}} </label>
              <label v-if="item.isEmpty" class="label-show"><span >&nbsp;</span>{{item.label}} </label>
              <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
              <datePicker v-if="item.type === 'timer'" :dateOpt="item.options" @getDatePicker="getFrom" :ref="item.options.key"></datePicker>
            </li>
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitDevice">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
    </div>
</template>
<script>
    import info from './Info.vue'
    import searchTree from '../../childComponent/leftSearchMenu.vue'
    export default {
        data() {
            return {
                showTable: false,
                searchParam: { //查询table的参数对象
                  deviceType: '07',
                  nodeId: '',
                  nodeType:'',
                  isSearchChild: 1,
                  isOnline: ''
                },
                clickEvent: true, //阻止单击事件重复提交
                deepSearchData: '',
                showInfo: false, //是否显示详情
            
                controls: [],
                searchType: 'deviceNo',
                selected: '', //table选中行数据
                tableHeight: '',
                FormOpt: [
                 
                ],
                TableOpt: {
                    rowName: 'data',
                    column: [],
//                    method: 'get',
//                    url: 'dist/json/equipment.json',
                    url: 'deviceInfo/queryListNew',
                    queryParam: {deviceType: '07', isSearchChild: 1},
                    method: 'post',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20,40,60],
                    sortName: 'createTime',
                    sortOrder: 'desc'
                },
                formData: { //设备信息对象
                  remark: '',
                  deviceNo: '',
                  deviceFactory: '',
                  installDate: '',
                  webSiteIp: '',
                  webSitePort: '',
                  orgNo: '',
                  apn: '',
                  installAddr: ''
                },
                showDialog: true
            }
        },
        components: {
            info,
            searchTree
        },
        computed: {
            addList() {
                let sysSet = localStorage.systemConfig ? JSON.parse(localStorage.systemConfig) : this.$store.state.systemConfig;
                let width = 'calc(100% - 6.5em)';
                return  [
                  {classes: 'halfWidth', type: 'input', isEmpty: false, label: '集中器号',
                    options: {type: 'normalInput',  placeHolder: '9位数字，如 000100001', key: 'formData.deviceNo', width: width,
                      regular: 'zdNo',length: 9,  errorMsg: '集中器编号：9位数字，取值范围：前4位[0001-9999],后5位[00001-65535]',
                      limit: 'number'}},
                  {classes: 'halfWidth hasMarginLi',type: 'timer', isEmpty: true, label: '安装日期', options: {key: 'formData.installDate',  width: width}},
                  {classes: 'halfWidth', type: 'input', isEmpty: true, label: '设备厂家', options: {type: 'normalInput',    width: width,placeHolder: '50个字符内', key: 'formData.deviceFactory', regular: '', length: 50, errorMsg: '格式：包含文字、字母、数字、-、（、）'}},
                  {classes: 'halfWidth hasMarginLi',type: 'select', isEmpty: false, label: '部门机构',
                    options: {
                      key: 'formData.orgNo',
                      type: 'complexSelect',
                      cs: 'server',
                      url: 'org/queryOrgTreeInfo',
                      method: 'post',
                      width: width,
                      text: 'orgName',
                      value: 'orgNo'
                    }},
                  {classes: 'halfWidth',type: 'input', isEmpty: false, label: '主站IP', options: {type: 'normalInput',  width: width, value: sysSet.frontProcessorIp, key: 'formData.webSiteIp', regular: 'ip', length: 15, errorMsg: '主站IP：如10.10.10.250'}},
                  {classes: 'halfWidth hasMarginLi',type: 'input', isEmpty: false ,label: '主站端口', options: {type: 'normalInput',  width: width,value: sysSet.frontProcessorPort, key: 'formData.webSitePort', length: 4, regular: 'port', errorMsg: '主站端口：4位数字，如8080', limit: 'number'}},
                  {classes: 'halfWidth',type: 'input', isEmpty: false, label: '通道APN', options: {type: 'normalInput',  width: width,value: sysSet.apn, key: 'formData.apn', regular: 'apn', length: 10,  errorMsg: '通道APN：大写英文', limit: 'upperCase'}},
                  {classes: 'halfWidth hasMarginLi',type: 'input', isEmpty: true, label: '安装位置', options: {type: 'normalInput', width: width, placeHolder: '50个字符内',length: 50,  key: 'formData.installAddr', regular: '', errorMsg: '格式：包含文字、字母、数字、-、（、）'}},
                  {classes: 'allWidth',type: 'input', isEmpty: true, label: '备注信息', options: {type: 'textArea', width: width,  placeHolder: '200个字符内', key: 'formData.remark', regular: '', errorMsg: '备注信息已超出200个字符'}}
                ];
            },
            columns() {
              let arr = [
                        {field: 'state',type: 'checkBox', textAlign: 'center',  width: '60'},
                        {
                            title: '状态',
                            field: 'isOnline',
                            textAlign: 'center',
                            sortable: false,
                            width: '100',
                            type: 'string',
                            formatter: function (val, index, row) {
                                let template;
                                if (val === 0) {
                                    template = '<span class="red">离线</span>';
                                } else {
                                    template = '<span class="green">在线</span>';
                                }
                                return  template
                            }
                        },
                        {title: '设备编号', field: 'deviceNo', textAlign: 'center', width: '120', sortable: false},
                        {
                          title: '设备类型',
                          field: 'deviceType',
                          textAlign: 'center',
                          width: '100',
                          sortable: false,
                          type: 'string',
                          formatter(val, index, item, obj) {
                              return obj.$parent.getEnumItem('allDeviceType', val).text
                          }
                        },
                        {title: '创建时间', field: 'createTime', textAlign: 'center', width: '130', sortable: false,  type: 'string', formatter(val, index, item, obj) {
                            if (val !== '' && val !== null) {
                                
                                return obj.$parent.getTrueDate(val, false)
                            }
                        }},
                    
                        {title: '设备厂家', field: 'deviceFactory', textAlign: 'center', width: '250', sortable: false},
                        {title: '安装位置', field: 'installAddr', textAlign: 'center', width: '250', sortable: false},
                        {title: '备注', field: 'remark', align: 'center', textAlign: 'middle', width: '200'}
                    ]
                 // if(this.searchParam.deviceType === '04' ) {
                     arr.splice(5, 0, {
                       title: '最后一次在线时间', field: 'lastOnlineTime', textAlign: 'center', width: '160', sortable: false,  type: 'string', formatter(val, index, item, obj) {
                            if (val !== '' && val !== null && typeof val !== 'undefined') {
                                return obj.$parent.getTrueDate(val)
                            }
                        }
                     })
                //  }
             return arr;  
            }
        },
        methods: {
            getIsOnline: function (val) {
               this.searchParam.isOnline = val;
            },
            getMeterType: function (val) {
               this.searchParam.deviceType = val;
               //this.getParams(1);
            },
            noDevice: function () {
               this.searchParam.nodeId = '';
               this.searchParam.nodeType = 1;
               this.getParams(1);
            },
            getSelectNode: function(val) {

              this.searchParam.nodeId = val.nodeId;
              this.searchParam.nodeType = val.nodeType || 1;
              if(!this.showTable) {
                this.TableOpt.queryParam.nodeId = val.nodeId;
                this.TableOpt.queryParam.nodeType = val.nodeType
                this.showTable = true;

              } else {
                this.getParams(1);
              }
            },
            //提交设备数据
            submitDevice: function() {
                let that = this;
                that.regularTest({
                  emptyKey: ['deviceNo', 'orgNo', 'webSiteIp', 'webSitePort', 'apn'],
                  prefix: 'formData',
                  callback: function () {
                      that.getRequest({
                        url: 'deviceInfo/addJzq',
                        method: 'post',
                        param: that.formData,
                        success: function (data) {
                            that.$refs.alerts.show({
                              msg: data.message
                            });
                            that.cancelDialog();
                            that.clearSearch();
                        },
                        error: function (data) {
                          that.$refs.alerts.show({
                            msg: data.message
                          });
                        }
                      })
                  }
                })
            },
            getFrom: function (val) {
              if (val.combine) {
                if (this.searchParam[val.key.split('.')[1]] !== val.val ) {
                  this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'isOnline']);
                  this.getOption(val, true);
                  this.getParams(1);
                }
              } else {
                this.getOption(val, true);
              }
            },
            removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                if (key === 'isOnline') {
                    this.$refs['selects'][1].constant = {text: '全部状态', value: ''};
                    this.searchParam.isOnline = '';
                }
               else if (key === 'deviceType') {
                    this.$refs['selects'][0].constant =    {text: '全部类型', value: ''};
                    this.searchParam.deviceType = '';
                }
                else {
                    this.searchParam[key] = this.$refs['inputs'][0].search = '';
                   this.getParams(1);
                }
            },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams();
            },
            cancelDialog: function ()  {
              this.showDialog = false;
              this.formData = this.cleanSearchData(this.formData);
              this.$refs.addEquip.close()
            },
          //清除所有的选择条件
            clearSearch: function () {
                let that = this;
                that.getParams(1);
            },
            //添加设备
            addEquipment: function () {
                this.showDialog = true;
                let that = this;
                setTimeout(function () {
                  that.$refs.addEquip.show()
                }, 0)
            },
            //删除设备
            removeEquipment: function () {
                let that = this;
                this.getConfirm({
                  emptyMsg: '请先选择要删除的设备!',
                  confirmMsg: '确定要对选中的设备进行删除操作吗？',
                  callback: {
                     confirm: function () {
                      that.deleteDevice();
                      }
                  }
                });
            },
            deleteDevice: function () {
                let that = this;
                this.deleteData({
                    url: 'deviceInfo/batchDelete',
                    params: ['deviceCno'],
                    callback: function () {
                       that.clearSearch();
                    },
                    error: function () {
                      that.clearSearch();
                    }
                })
            },
            getParams: function (pageNumber) {
              this.deepSearchData = [];
              let arr = this.getSearchData(this.searchParam);
              let labelName = '';
              for(let key in arr) {
                let value = arr[key];
                switch (key) {
                  case 'isOnline':
                    labelName = '是否在线';
                    value = this.getEnumItem('isOnline', value).text;
                    break;
                  case 'deviceType':
                    labelName = '设备类型';
                    value = this.getEnumItem('allDeviceType', value).text;
                    break;
                  case 'deviceNo':
                    labelName = '设备编号';
                    break;
                  case 'deviceFactory':
                    labelName = '设备厂家';
                    break;
                }
                this.deepSearchData.push({label: labelName, value: value, key: key, flag: true})
              }
              this.getHeight('calc(100% - 4.5em)');
      
              this.searchTable(arr, pageNumber);
            },
            clickTableRow: function (val) {
                this.row = val.val;
                this.showInfo = true
            },
            windowChange: function () {
               let that = this;
              that.getHeight('calc(100% - 4.5em)');
              // window.onresize = function () {
              //   that.getHeight('calc(100% - 4.5em)');
              // }
            },
            init: function () {
                let that = this;
                // window.onresize = function () {
                //   that.windowChange()
                // }
            }
        },
        created: function () {
          this.TableOpt.column = this.columns;
        },
        mounted: function () {
            let that = this;
            that.isIn();
            that.getAction();

        },
        watch: {
          
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
  .right-container .center-container .noback-Container{
    background: transparent;
    padding:0;
    width:100%;
  }
</style>



